body{
  align-items: center;
}
ul{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  margin: auto;
  list-style: none;
}

.component-header {
  padding: 15px;
  position: relative;
  font-weight: 400;
  font-family: Open Sans,Helvetica Neue,Helvetica,Arial,sans-serif;
  font-size: 32px;
  text-align: center;
  color: #333;
}

.component-search-input input {
  border-radius: 4px;
  border: 1px solid #bbb;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 18px;
  padding: 10px 8px;
  width: 100%;
}

.component-emoji-result-row {
  border-bottom: 1px solid #ccc;
  padding: 10px;
  height: 32px;
  position: relative;
  font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
  cursor: pointer;
}
.component-emoji-result-row:hover {
  background-color: #eee;
}

.component-emoji-result-row .title {
  position: relative;
  top: -8px;
}

.component-emoji-result-row img {
  width: 32px;
  height: 32px;
  padding-right: 10px;
}

.component-emoji-result-row .info {
  float: right;
  position: relative;
  top: 8px;
  right: 10px;
  color: #ccc;
  display: none;
}
.component-emoji-result-row:hover .info {
  display: inline-block;
}
